body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    color: #8B8A8A;
}

html, body, #login-page {
    height: 100%;
    box-sizing: border-box;
}

#login-page {
    display: flex;
}
.login-page-body {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    z-index: 2;
}

.page-main-cover {
    width: 100%;
    height: 300px;
    top: 0px;
    left: 0px;
    position: absolute;
    z-index: 1;
    background: url(/deploy/static/media/main-bg.e1e4e999.jpg) no-repeat 0px 50%, #ffffff;
    background-size: cover;
    border-radius: 0 0 15% 15%;
}
.login-header {
    display: flex;
    flex-direction: row;
    padding: 14px 20px 14px 20px;
}

.logo-wrapper {
    display: flex;
    flex: 1 1;
    flex-direction: row;
    justify-content: center;
}

.logo-wrapper .bet25-logo {
    margin-left: -70px;
}

.back-button {
    width: 60px;
    height: 60px;
    background: transparent;
    border: none;
    position: relative;
    cursor: pointer;
}

.back-arrow {
    color: #888888;
}

.back-button:hover .back-arrow {
    color: #666666;
}
.bet25-logo {
    width: 150px;
    height: 61px;
    background: url(/deploy/static/media/bet25-logo.fb22826f.png) no-repeat 0px 0px;
    background-size: cover;
}
.login-form-wrapper {
    display: flex;
    flex: 1 1;
    flex-direction: column;
}

.form-fields{
    background: white;
    max-width: 441px;
    min-width: 360px;
    box-shadow: 1px 1px 30px #DBDBDB;
    padding: 45px 40px 22px 40px;
    box-sizing: border-box;
}

.form-fields-wrapper {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.login-actions-wrapper {
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 20px;
}

.login-field {
    height: 65px;
    display: block;
    border: none;
    box-shadow: 0px 0px 26px #EEEEEE;
    padding: 0px 20px 0px 50px;
    font-size: 26px;
    margin: 0 0 38px 0;
    color: #666666;
    box-sizing: border-box;
    width: 100%;
}

.login-field::-webkit-input-placeholder {
    color: #B0B0B0;
}

.user-name {
    background: url(data:image/png;base64,) no-repeat 15px 18px;
}

.password {
    background: url(data:image/png;base64,) no-repeat 15px 20px;
}

.forgot-password {
    font-size: 20px;
    margin: 0 0 0 140px;
}

.login-button {
    background: #FFA300;
    color: #ffffff;
    font-size: 26px;
    text-align: center;
    padding: 25px;
    border: none;
    text-transform: uppercase;
    font-weight: bold;
    width: 315px;
    margin-bottom: 35px;
    cursor: pointer;
}

.login-button:not(.disabled):hover{
    background: #FF9000;
}

.login-button.disabled {
    background: #f5c36a;
    cursor: default;
}
.bet25-login-nem {
    justify-content: center;
    align-items: center;
}

.log-ind-med-link {
    font-size: 16px;
    text-decoration: none;
    margin-right: 10px;
}

.nem_id_logo {
    width: 75px;
    height: 17px;
    display: inline-block;
    background: url(data:image/png;base64,) no-repeat 0px -29px;
    background-size: cover;
}
